<template>
  <v-container fluid>
    <v-layout row>
      <v-flex md8 offset-md2>

        <v-banner>
          <v-icon
            slot="icon"
            color="warning"
            size="42"
          >mdi-plus-box</v-icon>
          Jika data yang ditampilkan kurang lengkap, Anda juga bisa melengkapi data RT/RW di desa Kolhua.

          <template v-slot:actions>
            <v-btn
              color="primary"
              text
              @click="$router.push('/admin/data/add')"
            >
              Tambah
            </v-btn>
          </template>
        </v-banner>

        <div v-if="!error">
          <v-toolbar
            flat
            class="mt-2"
          >
            <v-text-field
              v-model="keyword"
              class="mx-4"
              flat
              hide-details
              label="Search"
              prepend-inner-icon="search"
            ></v-text-field>
            <v-tabs
              slot="extension"
              centered
              v-model="currentTab"
            >
              <v-tab>Semua</v-tab>
              <v-tab>RW</v-tab>
              <v-tab>RT</v-tab>
            </v-tabs>
          </v-toolbar>

          <v-simple-table
            fixed-header
            class="mt-2 elevation-2"
          >
            <thead>
              <tr class="grey lighten-3">
                <td></td>
                <td class="py-4">Nama</td>
                <td>Alamat</td>
                <td>Latitude</td>
                <td>Longitude</td>
                <td></td>
              </tr>
            </thead>
            <tbody>
              <tr
                v-for="item in filtered"
                :key="`data-tr-${item._id}`"
              >
                <td class="py-2" width="120">
                  <div
                    class="white--text body-2 font-weight-bold pa-2 text-uppercase"
                    :class="item.jabatan == 'rw' ? 'green' : 'blue'"
                  >
                      {{ item.jabatan }} - {{ item.lokasiJabatan }}
                  </div>
                </td>
                <td>
                  <div class="body-2 font-weight-bold">{{ item.nama }}</div>
                </td>
                <td>
                  <div class="caption">{{ item.alamat }}</div>
                </td>
                <td>
                  <div class="body-2">{{ item.latitude }}</div>
                </td>
                <td>
                  <div class="body-2">{{ item.longitude }}</div>
                </td>
                <td>
                  <v-menu offset-y>
                    <template v-slot:activator="{ on }">
                      <v-icon v-on="on">mdi-dots-horizontal</v-icon>
                    </template>
                    <v-list dense>
                      <v-list-item :to="`/admin/data/update/${item._id}`">
                        <v-list-item-subtitle>
                          Edit
                        </v-list-item-subtitle>
                      </v-list-item>
                      <v-list-item @click="showDelWarn(item._id)">
                        <v-list-item-subtitle>
                          Hapus
                        </v-list-item-subtitle>
                      </v-list-item>
                    </v-list>
                  </v-menu>
                </td>
              </tr>
            </tbody>
          </v-simple-table>
        </div>

        <div v-else>
          <v-sheet>
            <v-layout column justify-center align-center class="pa-4">
              <v-icon
                x-large
                color="red"
              >mdi-alert-circle</v-icon>

              <div class="headline red--text text--darken-3 text-xs-center">Gagal memuat data</div>

              <v-btn outlined class="my-2" @click="loadData">
                Reload
                <v-icon right>mdi-reload</v-icon>
              </v-btn>
            </v-layout>
          </v-sheet>
        </div>

      </v-flex>
    </v-layout>

    <v-overlay v-model="overlayDelWarn">
      <v-banner width="500">
        Anda akan menghapus data
        <template v-slot:actions>
          <v-btn text @click="remove(delId)">Ya</v-btn>
          <v-btn text @click="overlayDelWarn = false">Tidak</v-btn>
        </template>
      </v-banner>
    </v-overlay>

    <router-view></router-view>

  </v-container>
</template>

<script>
import Vue from "vue";
import { node as nodeRepo } from "@/services/repo";
import evbus from "@/evbus";

const isRw = (t) => t.jabatan === 'rw';
const isRt = (t) => t.jabatan === 'rt';
const allPass = t => true;

export default Vue.extend({
  name: "ListData",
  data: () => ({
    currentTab: 0,
    dialogAddData: false,
    items: [],
    error: false,
    keyword: '',
    overlayDelWarn: false,
    delId: undefined,
  }),
  methods: {
    loadData() {
      nodeRepo.find({ jabatan: "all" })
        .then(items => {
          this.error = false;
          this.items = items;
        }).catch(err => {
          this.error = true;
        });
    },
    remove(id) {
      nodeRepo.delete({ id })
        .then(() => {
          console.log("this=", this);
          this.loadData();
        });
    },
    showDelWarn(id) {
      console.log("id=", id);
      this.delId = id;
      this.overlayDelWarn = true;
    }
  },
  computed: {
    filtered() {
      const keywordUpper = this.keyword.toUpperCase();
      const activeTab = this.currentTab;
      const items = this.items;
      
      let jabatanFilter;

      switch (activeTab) {
        case 0:
          jabatanFilter = allPass;
          break;
        case 1:
          jabatanFilter = isRw;
          break;
        case 2:
          jabatanFilter = isRt;
          break;
        default:
          throw new Error(`Unknown tab: ${activeTab}`);
      }

      const filterFunc = t => {
        if (!jabatanFilter(t)) return false;
        const namaUpper = t.nama.toUpperCase();
        return namaUpper.startsWith(keywordUpper);
      }
      return items.filter(filterFunc);
    }
  },
  mounted() {
    this.loadData();
    evbus.$on("admin-data-reload", () => {
      this.loadData();
    });
  },
  beforeDestroy() {
    evbus.$off("admin-data-reload");
  }
});
</script>

